@import url(//fonts.googleapis.com/css?family=Vibur);
@media screen and (max-width: 587px) {
    .slider{
        width: 40px;
        height: 40px;
    }
    .box{
        width: 100%;
        height: 100%;
    }
    .corner{
        visibility: hidden;
    }
    .img{
        -webkit-transform: translateY(26px);
                transform: translateY(26px);
    }

    /* skilles */
    .skilles{
        width: 400px;
        height: 300px;
        display: grid;
        -webkit-columns: 15px 15px;
                columns: 15px 15px;
        
        rows: 15px 15px;
        background-color: rgba(0, 0, 255, 0.151);
    }
    .logo{
        width: 40px;
        height: 40px;
        border-radius: 50%;
    }
    .item{
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        -webkit-align-items: center;
                align-items: center;
    }
    .full-progress{
        width: 200px;
        background-color: white;
    }
    .full-progress,.progress{
        height: 5px;
        border-radius: 20px;
    }
    .progress{
        
        background: linear-gradient(
            90deg,
            #da22ff,
            #9733ee
        );
        
    }
}
@media screen and (min-width: 588px) {
    /* skilles */
.skilles{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    width: 400px;
    height: 300px;
}
.logo{
    width: 40px;
    height: 40px;
    border-radius: 50%;
}
.item{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    height: 50px;
}
.full-progress{
    width: 200px;
    background-color: white;
}
.full-progress,.progress{
    height: 5px;
    border-radius: 20px;
}
.progress{
    
    background: linear-gradient(
        90deg,
        #da22ff,
        #9733ee
    );
    
}
}
@media screen and (max-width: 650px) {
    .home{
        -webkit-flex-direction: column;
                flex-direction: column;
    }
}
@media screen and (min-width: 651px) {
    .home{
        -webkit-flex-direction: row;
                flex-direction: row;
    }
}

@media screen and (max-width: 799px) {
    .slider{
        width: 40px;
        height: 40px;
    }
    .box{
        width: 90%;
        height: 90%;
    }
}
@media screen and (min-width: 800px) {
    .slider{
        width: 0px;
        height: 0px;
    }
}
@media screen and (min-width: 1200px) {
    body{
        background-color: blue;
    }
    .slider{
        width: 0px;
        height: 0px;
    }
}
