/* blink */
.aaa{
  width: 100%;
  height: 100%;
}
.head{
  position: absolute;
  width: 100%;
  height: 100px;
  display: grid;
  justify-items: center;
  -webkit-align-items: center;
          align-items: center;
  font: 200 40px "Vibur";
}
b{
  color: #eff6fd;
  text-shadow: 0 -40px 100px, 0 0 2px, 0 0 1em #0a3989, 0 0 0.5em #1e72cf, 0 0 0.1em #2873d1, 0 10px 3px #000;
}
.head b span{
  -webkit-animation: blink linear infinite 2s;
          animation: blink linear infinite 2s;
}
@-webkit-keyframes blink {
  78% {
    color: inherit;
    text-shadow: inherit;
  }
  79%{
     color: #333;
  }
  80% {
    
    text-shadow: none;
  }
  81% {
    color: inherit;
    text-shadow: inherit;
  }
  82% {
    color: #333;
    text-shadow: none;
  }
  83% {
    color: inherit;
    text-shadow: inherit;
  }
  92% {
    color: #333;
    text-shadow: none;
  }
  92.5% {
    color: inherit;
    text-shadow: inherit;
  }
}
@keyframes blink {
  78% {
    color: inherit;
    text-shadow: inherit;
  }
  79%{
     color: #333;
  }
  80% {
    
    text-shadow: none;
  }
  81% {
    color: inherit;
    text-shadow: inherit;
  }
  82% {
    color: #333;
    text-shadow: none;
  }
  83% {
    color: inherit;
    text-shadow: inherit;
  }
  92% {
    color: #333;
    text-shadow: none;
  }
  92.5% {
    color: inherit;
    text-shadow: inherit;
  }
}
.back{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
  z-index: -1;
}


.main{
  width: 100%;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
/* Btns */
.btns{
  position: absolute;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100px;
  
  transition: all .9s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.btns2{
  width: 100%;
  height: 100%;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.highlite{
  width: 100%;
  height: 100%;
  background-color: #002951;
  background: linear-gradient(to right,#002951, #002951);
  z-index: -1;
}
.slider,.highlite,.ul{
  position: absolute;
}
.slider{
  transition: all .5s linear;
  border-radius: 50%;
  background-color: #00172d;
  position: absolute;
  right: 0;
  z-index: 2;
}
.ul{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: left;
          align-items: left;
  height: 70%;
}
.link{
  text-decoration: none;
  color: white;
}



/* box */
.box{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  
  /*z-index: -1;*/
}




/* Corner */
.corner{
  background-color: #00172D;
  width: 70px;
  height: 170px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  border-top-left-radius: 20px;
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 3;
}
.corner>*{
  width: 20px;
  height: 20px;
