@media screen and (max-width: 587px) {
    .slider{
        width: 40px;
        height: 40px;
    }
    .box{
        width: 100%;
        height: 100%;
    }
    .corner{
        visibility: hidden;
    }
    .img{
        transform: translateY(26px);
    }

    /* skilles */
    .skilles{
        width: 400px;
        height: 300px;
        display: grid;
        columns: 15px 15px;
        
        rows: 15px 15px;
        background-color: rgba(0, 0, 255, 0.151);
    }
    .logo{
        width: 40px;
        height: 40px;
        border-radius: 50%;
    }
    .item{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
    .full-progress{
        width: 200px;
        background-color: white;
    }
    .full-progress,.progress{
        height: 5px;
        border-radius: 20px;
    }
    .progress{
        
        background: linear-gradient(
            90deg,
            #da22ff,
            #9733ee
        );
        
    }
}
@media screen and (min-width: 588px) {
    /* skilles */
.skilles{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 400px;
    height: 300px;
}
.logo{
    width: 40px;
    height: 40px;
    border-radius: 50%;
}
.item{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 50px;
}
.full-progress{
    width: 200px;
    background-color: white;
}
.full-progress,.progress{
    height: 5px;
    border-radius: 20px;
}
.progress{
    
    background: linear-gradient(
        90deg,
        #da22ff,
        #9733ee
    );
    
}
}
@media screen and (max-width: 650px) {
    .home{
        flex-direction: column;
    }
}
@media screen and (min-width: 651px) {
    .home{
        flex-direction: row;
    }
}

@media screen and (max-width: 799px) {
    .slider{
        width: 40px;
        height: 40px;
    }
    .box{
        width: 90%;
        height: 90%;
    }
}
@media screen and (min-width: 800px) {
    .slider{
        width: 0px;
        height: 0px;
    }
}
@media screen and (min-width: 1200px) {
    body{
        background-color: blue;
    }
    .slider{
        width: 0px;
        height: 0px;
    }
}